.pinKeyboard{
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 45%;
    left: 0;
    right: 0;
    transform: translateY(50%);

    ion-button{
        width: 75px;
        height: 75px;
        font-size: 20px;
        --border-style: none;
        --border-radius: 50%;

        &.backspace-button{
        --border-radius: 10px;
            font-size: 20px;
        }
    }
}