.auth-page{
    display: flex;
    flex-direction: column;
    --overflow: hidden;
    --background: radial-gradient(circle, rgba(134, 61, 246, 1) 0%, rgba(86, 102, 247, 1) 100%);
    .header-page{
        width: 100%;
        height: 15%;
        display: flex;
        align-items: center;
        justify-content: center;

        h1{
            font-size: 28px;
            font-weight: bold;
            color: white;
            margin: 0;
            text-align: center;
        }
    }


    .container{
        width: 100%;
        height: 85%;
        border-top-left-radius: 30px;
        border-top-right-radius: 30px;
        background-color: white;
        padding: 48px;
        position: relative;

        .pin{
            width: 100%;
            text-align: center;
            font-size: 25px;
            color: black;
            display: flex;
            align-items: center;
            justify-content: center;


            .underscore{
                margin: 0 5px;
            }

            .letter{
                width: 20px;
                height: 20px;
                border-radius: 100%;
                background-color: black;
                margin: 0 5px;
            }
        }
    }
}

@media screen and (max-width: 376px) and (max-height: 668px) {
    .auth-page{
        .header-page{
            h1{
                font-size: 22px;
            }
        }

        .container{
            padding: 10px;
        }
    }
}